<template>
  <section class="main-content">
    <div v-for="item in dapps" class="main-container">
      <p class="title">{{ item.type }}</p>
      <div class="dapp-container">
        <div v-for="dapp in item.list" class="dapp">
          <div class="dapp-title-container">
            <div class="dapp-logo-container">
              <img :src="require(`../assets/img/logo/${dapp['icon_name']}.png`)">
            </div>
            <div class="dapp-title-content" @click="toUrl(dapp.url)">
              <div class="dapp-title-content-inner">
                <div class="dapp-title-block">
                  <p class="dapp-title">{{ dapp.title }}</p>
                  <div class="enter pic-full"></div>
                </div>
                <p class="dapp-url">{{ dapp.url }}</p>
              </div>
            </div>
          </div>
          <div class="add-button-container" @click="addDApp(dapp)">
            <div class="add-button">{{ $t('add') }}</div>
          </div>
        </div>
        <div v-for="emptyDapp in 3" class="dapp empty-dapp"></div>
      </div>
    </div>
  </section>
</template>

<script>
const tp = require('tp-js-sdk')
export default {
  name: "main-content",
  props: {
    dapps: {
      type: Array
    }
  },
  methods: {
    addDApp(dapp) {
      if (this.device === 'pc') {
        this.$emit('selectDApp', dapp)
      } else {
        const params = {
          ...dapp,
          protocol: "TIP-001",
          protocol_version: "1.0",
          icon_url: `https://raw.githubusercontent.com/dappcom/dapps/main/src/assets/img/logo/${dapp['icon_name']}.png`
        }
        delete params['icon_name']
        if (this.isTP) {
          tp.addDAppToDiscover(params).then(res=>{}).catch(err=>{})
        } else {
          location.href = 'tpdiscover://add?params=' + encodeURI(JSON.stringify(params))
        }
      }
    },
    toUrl(url) {
      window.open(url)
    }
  }
}
</script>

<style lang="less">
